"use client";
import React, { useState, useEffect, useRef } from "react";
import { Heart, Sun, Moon } from "lucide-react";
import { motion } from "framer-motion";
import Link from "next/link";
import * as THREE from "three";

const Home = () => {
  const [isDarkMode, setIsDarkMode] = useState(false);
  const threeContainerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (typeof window !== "undefined" && threeContainerRef.current) {
      const scene = new THREE.Scene();
      const camera = new THREE.PerspectiveCamera(
        75,
        window.innerWidth / window.innerHeight,
        0.1,
        1000
      );
      const renderer = new THREE.WebGLRenderer({ alpha: true });
      renderer.setSize(
        threeContainerRef.current.clientWidth,
        threeContainerRef.current.clientHeight
      );
      threeContainerRef.current.appendChild(renderer.domElement);

      const geometry = new THREE.SphereGeometry(1, 32, 32);
      const material = new THREE.MeshPhongMaterial({
        color: 0xff0000,
        shininess: 100,
      });
      const bloodDrop = new THREE.Mesh(geometry, material);
      scene.add(bloodDrop);

      const light = new THREE.DirectionalLight(0xffffff, 1);
      light.position.set(5, 5, 5).normalize();
      scene.add(light);

      camera.position.z = 5;

      const animate = () => {
        requestAnimationFrame(animate);
        bloodDrop.rotation.x += 0.01;
        bloodDrop.rotation.y += 0.01;
        renderer.render(scene, camera);
      };
      animate();

      return () => {
        threeContainerRef.current?.removeChild(renderer.domElement);
      };
    }
  }, []);

  const containerVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: {
      opacity: 1,
      y: 0,
      transition: {
        duration: 0.6,
        when: "beforeChildren",
        staggerChildren: 0.2,
      },
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, x: -20 },
    visible: {
      opacity: 1,
      x: 0,
      transition: { duration: 0.5 },
    },
  };

  const toggleDarkMode = () => {
    setIsDarkMode(!isDarkMode);
  };

  return (
    <motion.div
      initial="hidden"
      animate="visible"
      variants={containerVariants}
      className={`h-screen w-screen ${
        isDarkMode
          ? "bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900"
          : "bg-gradient-to-br from-red-50 via-white to-red-50"
      } flex flex-col justify-center items-center py-12 sm:px-6 lg:px-8`}
    >
      {/* Header */}
      <motion.header
        className="w-full fixed top-0 left-0 right-0 bg-white/80 backdrop-blur-sm shadow-sm"
        variants={itemVariants}
      >
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4 flex justify-between items-center">
          <Link href="/">
            <motion.div
              className="text-xl font-bold text-red-600"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              BloodLink
            </motion.div>
          </Link>
          <nav className="space-x-4">
            <Link href="/about">
              <motion.span
                className="text-gray-700 hover:text-red-600 transition-colors duration-200"
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                About
              </motion.span>
            </Link>
            <Link href="/contact">
              <motion.span
                className="text-gray-700 hover:text-red-600 transition-colors duration-200"
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                Contact
              </motion.span>
            </Link>
          </nav>
        </div>
      </motion.header>

      {/* Dark Mode Toggle */}
      <motion.button
        onClick={toggleDarkMode}
        className="fixed top-4 right-4 p-2 rounded-full bg-white/80 backdrop-blur-sm shadow-lg hover:bg-gray-100 transition-colors duration-200"
        whileHover={{ scale: 1.1 }}
        whileTap={{ scale: 0.9 }}
      >
        {isDarkMode ? (
          <Sun className="h-6 w-6 text-yellow-500" />
        ) : (
          <Moon className="h-6 w-6 text-gray-800" />
        )}
      </motion.button>

      {/* Hero Section */}
      <motion.div className="text-center" variants={containerVariants}>
        {/* 3D Blood Drop */}
        <div ref={threeContainerRef} className="w-64 h-64 mx-auto"></div>

        {/* Title */}
        <motion.h1
          variants={itemVariants}
          className={`mt-6 text-4xl font-bold ${
            isDarkMode ? "text-white" : "text-gray-900"
          } drop-shadow-sm`}
        >
          Welcome to BloodLink
        </motion.h1>

        {/* Subtitle */}
        <motion.p
          variants={itemVariants}
          className={`mt-2 text-lg ${
            isDarkMode ? "text-gray-300" : "text-gray-600"
          }`}
        >
          Connecting donors with those in need
        </motion.p>

        {/* Call-to-Action Button */}
        <motion.div variants={itemVariants} className="mt-8">
          <Link href="/donor-registration">
            <motion.button
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
              className={`py-3 px-6 rounded-lg shadow-sm text-sm font-medium text-white ${
                isDarkMode
                  ? "bg-gradient-to-r from-red-700 to-red-600"
                  : "bg-gradient-to-r from-red-600 to-red-500"
              } hover:from-red-700 hover:to-red-600 transition-all duration-200`}
            >
              Become a Donor
            </motion.button>
          </Link>
        </motion.div>
      </motion.div>

      {/* Footer */}
      <motion.footer
        className="fixed bottom-0 left-0 right-0 bg-white/80 backdrop-blur-sm shadow-sm"
        variants={itemVariants}
      >
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4 text-center">
          <p className="text-sm text-gray-600">
            &copy; {new Date().getFullYear()} BloodLink. All rights reserved.
          </p>
        </div>
      </motion.footer>
    </motion.div>
  );
};

export default Home;
